
.header {
  width: 100%;
  background-color: #fff;
  font-family: moderat,sans-serif;
  font-size: 16px;
}
.navbar {
  -webkit-box-pack: justify;
  -ms-flex-pack: justify;
  justify-content: space-between;
  -ms-flex-line-pack: center;
  align-content: center;
  width: 100%;
  height: 70px;
  max-width: 1280px;
  margin: 0 auto;
  padding: 5px 20px 10px;
}
.navbar, .navbar-left-container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.navbar-left-container {
  -webkit-box-pack: start;
  -ms-flex-pack: start;
  justify-content: flex-start;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  margin-left: -20px;
}
.navbar-menu-container, .navbar-right-container {
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
}
.navbar-right-container {
  display: none;
  -webkit-box-pack: start;
  -ms-flex-pack: start;
  justify-content: flex-start;
}
.navbar-cart-logo {
  width: 25px;
  height: 25px;
  -webkit-transform: scaleX(-1);
  transform: scaleX(-1);
}
.navbar-menu-container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: end;
  -ms-flex-pack: end;
  justify-content: flex-end;
  padding-top: 10px;
}
.navbar-menu-container, .navbar-right-container {
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
}
.navbar-link {
  padding-left: 15px;
}
.navbar-cart-count {
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  position: absolute;
  top: -9px;
  right: -11px;
  width: 20px;
  border-radius: 10px;
  color: #fff;
  background-color: #eb767d;
  font-size: 16px;
  font-weight: 700;
  text-align: center;
}
.navbar-cart-container {
  position: relative;
}
.reg-title{ margin-left: 150px;
}
.md-title{cursor: pointer
}
.ddd-actuve{border-bottom: 2px solid #00B7FF
}
.okTip{color: #53cd5f
}



